<template lang="html">
  <div id="map">

  </div>
</template>

<script>
import mapboxgl from 'mapbox-gl'
mapboxgl.accessToken = "pk.eyJ1IjoiY2FydG9uZyIsImEiOiJjazJldzVobGkwOWRxM2hzNTB1M3o2cG94In0.w7FyG31FWqXm3vXSh6WtxQ"
import {toGeoJSON} from '@/helpers/toGeoJSON'

export default {
  name: "TheMap",
  mounted(){
    this.initMap()
  },
  computed:{
    camps(){
      return {
        selectedCamp: toGeoJSON(this.$store.state.camps.filter(x => x.id == this.$store.state.selectedCamp)),
        otherCamps: toGeoJSON(this.$store.state.camps.filter(x => x.id != this.$store.state.selectedCamp))
      }
    },
    selectedCamp() {
      return this.$store.state.selectedCamp
    }
  },
  watch:{
    camps(){
      this.refreshCampsLayer()
    }
  },
  methods:{
    initMap(){
      this.map = new mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/mapbox/streets-v11', // stylesheet location
        center: [7.5, 9], // starting position [lng, lat]
        zoom: 4.7 // starting zoom
      })
      // Add camps
      this.map.on('load', this.refreshCampsLayer)
    },
    refreshCampsLayer(){
      if (this.map.getLayer('camps')) this.map.removeLayer('camps');
      if (this.map.getLayer('camp')) this.map.removeLayer('camp');
      if (this.map.getLayer('cluster-count')) this.map.removeLayer('cluster-count');
      if (this.map.getLayer('clusters')) this.map.removeLayer('clusters');
      if (this.map.getSource('otherCamps')) this.map.removeSource('otherCamps');
      if (this.map.getSource('selectedCamp')) this.map.removeSource('selectedCamp');
      this.map.loadImage(`https://raw.githubusercontent.com/0live/large_geojson-sample/master/default.png`, (error, image) => {
        if (error) throw error;
        if (!this.map.hasImage('default')) this.map.addImage('default', image);
        this.map.addSource('otherCamps', {
          'type': 'geojson',
          'data': this.camps.otherCamps,
          cluster: true,
          clusterMaxZoom: 14, // Max zoom to cluster points on
          clusterRadius: 50
          });
          this.map.addLayer({
            id: 'clusters',
            type: 'circle',
            source: 'otherCamps',
            filter: ['has', 'point_count'],
            paint: {
            'circle-color': [
              'step',
              ['get', 'point_count'],
              '#78b4ff',
              10,
              '#337fdd',
              30,
              '#286090'
            ],
            'circle-radius': [
              'step',
              ['get', 'point_count'],
              10,
              10,
              15,
              30,
              20
            ]
          }
        });
        this.map.addLayer({
          id: 'cluster-count',
          type: 'symbol',
          source: 'otherCamps',
          filter: ['has', 'point_count'],
          layout: {
            'text-field': '{point_count_abbreviated}',
            'text-font': ['Arial Unicode MS Bold'],
            'text-size': 12
          },
          paint: {
            "text-color": "#ffffff"
          }
        });
        this.map.on('click', 'clusters', (e) => {
          var features = this.map.queryRenderedFeatures(e.point, {
            layers: ['clusters']
          });
          var clusterId = features[0].properties.cluster_id;
          this.map.getSource('otherCamps').getClusterExpansionZoom(
            clusterId,
            (err, zoom) => {
              if (err) return;

              this.map.easeTo({
                center: features[0].geometry.coordinates,
                zoom: zoom
              });
            }
          );
        });
        this.map.addLayer({
          'id': 'camps',
          'type': 'symbol',
          'source': 'otherCamps',
          'filter': ['!', ['has', 'point_count']],
          'layout': {
            'icon-image': 'default',
            'icon-size': 0.025
          }
        });
      });
      this.map.loadImage(`https://raw.githubusercontent.com/0live/large_geojson-sample/master/default_selected.png`, (error, image) => {
        if (error) throw error;
        if (!this.map.hasImage('default_selected')) this.map.addImage('default_selected', image);
        this.map.addSource('selectedCamp', {
          'type': 'geojson',
          'data': this.camps.selectedCamp
          });
        this.map.addLayer({
          'id': 'camp',
          'type': 'symbol',
          'source': 'selectedCamp',
          'filter': ['!', ['has', 'point_count']],
          'layout': {
            'icon-image': 'default_selected',
            'icon-size': 0.035
          }
        })
      })


      this.map.on('click', 'camps', (e) => {
        this.$store.commit('setSelectedCamp', e.features[0].properties.id)
      })

      let popup = new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false
      });
      this.map.on('mouseenter', 'camps', (e) => {
        this.map.getCanvas().style.cursor = 'pointer';
        const coordinates = e.features[0].geometry.coordinates.slice()
        const description = `<h4 class="primary--text">${e.features[0].properties.name}</h4>`
        popup.setLngLat(coordinates).setHTML(description).addTo(this.map)
      });
      this.map.on('mouseleave', 'camps', () => {
        this.map.getCanvas().style.cursor = ''
        popup.remove()
      });
      this.map.on('mouseenter', 'camp', (e) => {
        this.map.getCanvas().style.cursor = 'pointer';
        const coordinates = e.features[0].geometry.coordinates.slice()
        const description = `<h4 class="primary--text">${e.features[0].properties.name}</h4>`
        popup.setLngLat(coordinates).setHTML(description).addTo(this.map)
      });
      this.map.on('mouseleave', 'camp', () => {
        this.map.getCanvas().style.cursor = ''
        popup.remove()
      });
    }
  }
}
</script>

<style lang="css" scoped>
  #map{
    width: 100%;
    height: 100%;
    border-radius: 15px;
  }

</style>
